.cards {
  align-self: center;
  grid-area: 2/2;
}

.card {
  display: grid;
  grid-template-areas: "img img img img img img" "h2 h2 h2 h2 h2 h2" "p p p p p p" "pri pri pri time time time" "cre cre cre cre cre cre";
  column-gap: 3.75rem;
  row-gap: 0.625rem;
  background-color: var(--verydarkblue-card-bg);
  color: var(--white);
  border-radius: 0.9375rem;
  padding: 1.25rem;
}
.card__image {
  grid-area: img;
  position: relative;
  max-width: 25rem;
  width: 100%;
  cursor: pointer;
}
.card__image:hover {
  opacity: 1;
}
.card__image img {
  display: block;
  max-width: 100%;
  height: auto;
  border-radius: 0.625rem;
}
@media (min-width: 71.25em) {
  .card__image img {
    display: unset;
  }
}
.card .overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.3s ease;
  border-radius: 0.625rem;
  cursor: pointer;
}
.card .overlay:hover {
  opacity: 0.5;
  background: var(--cyan);
}
.card .icon {
  color: var(--white);
  font-size: 6.25rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.card__name {
  grid-area: h2;
  color: var(--white);
  font-weight: 700;
  font-size: 1.25rem;
  text-align: left;
  padding-top: 1.25rem;
}
.card__name:hover {
  color: var(--cyan);
  cursor: pointer;
}
@media (min-width: 71.25em) {
  .card__name {
    font-size: 2.1875rem;
  }
}
.card__descript {
  grid-area: p;
  font-size: 1.125rem;
  font-weight: 300;
  color: var(--softblue);
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
}
@media (min-width: 71.25em) {
  .card__descript {
    font-size: 1.5625rem;
  }
}
.card__price {
  display: flex;
  grid-area: pri;
  color: var(--cyan);
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
@media (min-width: 71.25em) {
  .card__price {
    font-size: 1.5625rem;
  }
}
.card__price img {
  padding: 0.625rem;
}
@media (min-width: 71.25em) {
  .card__price img {
    width: 2.8125rem;
    height: 3.125rem;
  }
}
.card__time {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  grid-area: time;
  color: var(--softblue);
  width: 100%;
}
@media (min-width: 71.25em) {
  .card__time {
    font-size: 1.5625rem;
  }
}
.card__time img {
  padding: 0.625rem;
}
@media (min-width: 71.25em) {
  .card__time img {
    width: 3.75rem;
    height: 3.75rem;
  }
}
.card__creator {
  grid-area: cre;
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  border-top: 0.125rem solid var(--verydarkblue-line);
  padding: 1.25rem 0 1.25rem 0.3125rem;
  color: var(--softblue);
}
@media (min-width: 71.25em) {
  .card__creator {
    font-size: 1.5625rem;
  }
}
.card__creator img {
  border: 0.125rem solid var(--white);
  max-width: 100%;
  margin: 0.625rem;
  border-radius: 50%;
}
@media (min-width: 71.25em) {
  .card__creator img {
    width: 3.75rem;
    height: 3.75rem;
  }
}
.card__creator span {
  color: var(--white);
  padding-left: 0.3125rem;
  text-decoration: none;
}
.card__creator span:hover {
  color: var(--cyan);
  cursor: pointer;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  background: var(--verydarkblue-main-bg);
  font-family: var(--font-outfit);
}

:root {
  /* Primary Colors : */
  --softblue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  /* Neutral Colors : */
  --verydarkblue-main-bg: hsl(217, 54%, 16%);
  --verydarkblue-card-bg: hsl(217, 40%, 22%);
  --verydarkblue-line: hsl(215, 34%, 26%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
}
@media (min-width: 71.25em) {
  .container {
    padding: 0;
  }
}